import { formatDistance } from "date-fns";

export default function Table({ scrapes }) {
  const reversedScrapes = [...scrapes].reverse();
  return (
    <table>
      <thead>
        <tr>
          <td>Count</td>
          <td>Time</td>
        </tr>
      </thead>
      <tbody>
        {reversedScrapes.map((scrape, index) => {
          return (
            <tr key={scrape.date}>
              <td>{scrape.count}</td>
              <td>{formatDistance(new Date(scrape.date), new Date())}</td>
            </tr>
          );
        })}
      </tbody>
    </table>
  );
}
